<template>
  <ul class="entryList">
      <li v-for="(entry, index) in entries" v-bind:key="index">{{entry.message}}</li>
  </ul>
</template>

<script>
import { composeAPI } from "@iota/core";
const Converter = require("@iota/converter");

const iota = composeAPI({
  provider: "https://nodes.devnet.thetangle.org:443"
});

// Must be truly random & 81-trytes long.

export default {
  name: "EntryList",
  data() {
    return {
      entries: '',
      address: ''
    };
  },
  created() {
    this.address = localStorage.getItem("address");
    this.fetchEntries();
  },
  methods: {
    fetchEntries() {
        iota
        .findTransactionObjects({ addresses: [this.address] })
        .then(response => {
          this.entries = [];
          const msg = response
            .sort((a, b) => b.timestamp - a.timestamp)
            .map(tx => {
              let msg = tx.signatureMessageFragment;
              let entry = {
                  message: Converter.trytesToAscii(msg + "9"),
                  timestamp: tx.timestamp
              }
              this.entries.push(entry);
            });
        })
        .catch(err => {
          console.error(err);
        });
    }
  }
};
</script>

